.header{
  &._open-catalog{
    .header-catalog{
      opacity: 1;
      pointer-events: unset;
    }
    .mob-footer__item--catalog,
    .catalog-btn{
      ._view1{
        opacity: 0;
      }
      ._view2{
        opacity: 1;
      }
    }
  }
}
.header-catalog{
  position: absolute;
  top: 9em;
  left: 0;
  width: 100%;
  z-index: 10;
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  &__bg{
    display: none;
  }
  &__inner{
    border-radius: 1em;
    background-color: $white;
    display: grid;
    grid-template-columns: 27.5em calc(100% - 55em) 27.5em;
  }
  &__col{
    padding: 2.5em;
  }
  &__col2{
    padding: 2.5em;
    display: flex;
    flex-direction: column;
  }
  &__items{
    @include scroll;
    &::-webkit-scrollbar-track {
      background: $line;
    }

    &::-webkit-scrollbar-thumb {
      background: $primary;
    }
  }
  &__item{
    padding: .5em 1em;
    min-height: 2.875em;
    display: flex;
    align-items: center;
    cursor: pointer;
    transition: all .3s;
    border-radius: .1875em;
    margin-bottom: .5em;
    &:last-child{
      margin: 0;
    }
    img{
      width: 1.875em;
      margin-right: .625em;
    }
  }
  &__top{
    display: none;
  }
  @media screen and (min-width: 577px){
    &__col2{
    }
    ._hide-pc{
      display: none;
    }
    &__inner{
      //height: calc(100 * var(--vh) - 20em);
    }
    &__items{
      padding-bottom: 1em;
      height: calc(100 * var(--vh) - 24em);
      overflow-y: auto;
      padding-right: 1em;
    }
    &__item{
      border-radius: .25em;
      &._active,
      &:hover{
        background-color: $fon;
      }
    }
  }
}

.submenu{
  max-width: 55em;
  display: none;
  &__back{
    display: none;
  }
  &._active{
    display: block;
  }
  &__items{
    margin-top: 1.5em;
  }
  a{
    font-size: 1em;
    line-height: 150%;
  }

}


@media screen and (max-width: 992px){
  .header {
    &._open-catalog {
      .header-catalog__bg{
        display: block;
        background-color: $white;
      }
      .mob-footer__btn._toggle-catalog{
        path{
          stroke: $primary;
        }
      }
    }
    &._open-sub-catalog{
      .catalog-contacts{
        display: flex;
      }

    }
  }

  .header-catalog{
    box-shadow: none;
    height: calc(100 * var(--vh) - 3.625em);
    position: fixed;
    top: 0;
    left: 0;
    border-radius: 0;
    padding: 1.5em 1.875em;
    width: 100%;
    background-color: $white;
    .container{
      padding: 0;
    }
    &__items{
      height: calc(100 * var(--vh) - 14em);
      max-height: unset;
    }
    &__top{
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      padding: 1em 1.875em;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__close{
      width: 1.5em;
      @include center;
      margin-left: auto;
      img{
        width: 100%;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .header-catalog{
    .container{
      height: 100%;
    }
    &__bg{
      display: none;
    }
    &__top{
      z-index: 3;
      position: absolute;
      top: 1em;
      padding: 0 1em;
    }
    &__back{
      display: flex;
      align-items: center;
      color: $gray;
      opacity: 0;
      pointer-events: none;
      img{
        width: .75em;
        margin-right: .5em;
      }
    }
    &__inner {
      display: block;
      z-index: 1;
      height: 100%;
    }
    &__col{
      padding: 0;
    }
    &__col2{
      padding: 0;
      height: calc(100 * var(--vh) - 6.625em);
      max-height: unset;
      border: none;
      display: none;
    }
    &__items{
      margin-top: 1em;
      height: calc(100 * var(--vh) - 12.625em);
      overflow-y: auto;
    }
    &__item{
      min-height: unset;
      img{
        margin-right: 1.16em;
      }
      &._active,
      &:hover{
        background-color: transparent;
      }
    }
  }

  .submenu._active,
  .submenu{
    display: none;
  }

  .submenu{
    &__top{
      padding: .81em 1.5em;
      border-radius: 1em;
      background-color: $fon;
    }
    &__items{
      //grid-template-columns: repeat(2,calc(50% - .75em));
      margin-top: 1.25em;
      height: auto;
      max-height: calc(100 * var(--vh) - 12em);
      overflow-y: auto;
      @include scroll;
      padding-right: .25em;
      &::-webkit-scrollbar-track {
        background: $line;
      }

      &::-webkit-scrollbar-thumb {
        background: $primary;
      }
    }
    &__back{
      @include center;
      position: relative;
      z-index: 7;
      width: 2.5em;
      height: 2.5em;
      margin-right: 1.5em;
      border-radius: .5em;
      background-color: $white;
      img{
        width: .75em;
      }
    }
  }

  .header._open-sub-catalog{
    .header-catalog__col{
      display: none;
    }
    .header-catalog__col2{
      display: flex;
    }
    .submenu._active{
      display: block;
    }
    .header-catalog__back{
      opacity: 1;
      pointer-events: unset;
    }
  }
}


@media screen and (max-width: 576px){
  .header-catalog{
    padding: 1.5em 1em;
    &__item{
      padding-top: .5em;
      padding-bottom: .5em;
      margin-bottom: 0;
    }
  }

  .header._open-sub-catalog{
    .catalog-contacts{
      display: none;
    }
  }

  .submenu {
    &__top{
      padding: .75em 1em;
    }
    &__back{
      margin-right: 1em;
    }
    &__items {
      grid-template-columns: 100%;
      gap: 1em;
    }
  }
}
