.catalog-main{
  color: $white;
  .full-block{
    padding: 6.5em 0 2.5em;
  }
  .breadcrumbs{
    margin: 0 0 6.5em;
    color: rgba($white,.6);
    li{
      &:last-child{
        color: $white;
      }
    }
  }
}

.sort{
  display: flex;
  align-items: center;
  &__items{
    display: flex;
    align-items: center;
  }
  .sort-item{
    margin-left: 1em;
  }
}

.sort-item{
  label{
    color: $gray;
    display: flex;
    align-items: center;
    &:after{
      content: '';
      background-image: url(../img/sort.svg);
      @include bgImgSize(100%);
      opacity: 0;
      width: 0;
      height: 0;
    }
  }
  input:checked ~ label{
    color: $black;
    &:after{
      width: 1.125em;
      height: 1.125em;
      margin-left: .625em;
      opacity: 1;
    }
  }
}

.sort-dropdown{
  &__show{
    display: none;
  }
}

.catalog{
  &__col2{
    grid-column: span 5;
  }
  &__filter-btn{
    display: none;
  }
}

.filter{
  &__groups{
    padding: 1.25em;
    background-color: $fon;
    border-radius: .75em;
    @include grid100gap(1.5em);
  }
  &__top{
    display: none;
  }
}

.filter-group{
  &__bottom{
    margin-top: 1em;
  }
  &__checks{
    @include grid100gap(.75em);
    .m-check{
      display: inline-flex;
      margin-right: auto;
      label{
        min-height: 1.625em;
        padding-left: 2.25em;
        &:before{
          width: 1.625em;
          height: 1.625em;
        }
      }
    }
  }
  &__link{
    display: inline-block;
    margin-right: auto;
  }
  .m-search{
    margin-bottom: 1em;
    height: 3em;
  }
  .text-btn{
    ._view2{
      display: none;
    }
  }
  &:not(._show-all){
    .m-check:nth-child(6)~.m-check{
      display: none;
    }
    .filter-group__link:nth-child(6)~.filter-group__link{
      display: none;
    }
  }
  &._show-all{
    .text-btn{
      ._view1{
        display: none;
      }
      ._view2{
        display: flex;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .catalog-main{
    .full-block{
      padding-top: 5.625em;
    }
  }

  .catalog{
    &__inner{
      position: relative;
      padding-top: 6.25em;
    }
    &__grid{
      grid-template-columns: 100%;
      gap: 0;
    }
    &__col2{
      grid-column: span 1;
    }
    &__filter-btn{
      display: flex;
      margin-bottom: 1em;
      width: 100%;
    }
  }

  .filter{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $fon;
    z-index: 100;
    padding-top: 3.5em;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    &__top{
      position: fixed;
      top: 0;
      left: 0;
      height: 3.5em;
      width: 100%;
      padding: 0 1em;
      z-index: 10;
      background-color: $fon;
      border-bottom: $border;
      display: flex;
      align-items: center;
      justify-content: space-between;
      .h5{
        @include centerAbsolute;
      }
    }
    &__close{
      width: 1.5em;
      @include center;
      padding: .25em;
      img{
        width: 100%;
      }
    }
    &__scroll{
      height: 100%;
      overflow-y: auto;
      padding: 1em;
    }
    &__groups{
      padding: 0;
    }
  }

  ._show-filter{
    .filter{
      opacity: 1;
      pointer-events: unset;
    }
  }
}


@media screen and (max-width: 576px){
  .catalog-main{
    .full-block{
      padding: 5em 0 1.5em;
    }
    .breadcrumbs{
      margin-bottom: 4em;
    }
  }

  .catalog{
    &__inner{
      padding-top: 5.75em;
    }
  }

  .sort{
    &__title{
      display: none;
    }
    &__items{
      display: block;
    }
    .sort-item{
      margin: 0;
    }
  }

  .sort-item{
    label{
      padding: .625em;
      &:after{
        display: none;
      }
    }
    input:checked~label{
      color: $black;
    }
  }

  .sort-dropdown{
    margin-left: 1em;
    position: relative;
    z-index: 3;
    &__show{
      display: flex;
      align-items: center;
      cursor: pointer;
      img{
        width: 1.125em;
        margin-left: .625em;
      }
    }
    &__hidden{
      position: absolute;
      top: 100%;
      margin-top: .375em;
      right: 0;
      width: 13.75em;
      background-color: $white;
      box-shadow: 0 6px 16px rgba($black,.14);
      padding: .25em;
      border-radius: .375em;
      transition: all .3s;
      opacity: 0;
      pointer-events: none;
    }
    &._open{
      .sort-dropdown__hidden{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
}
