.filter-slider{
  @include lastM(0 0 .75em);
  &__item{
    min-height: 1.125em;
    padding: 0 .125em;
    display: flex;
    align-items: center;
  }
  .range-slider{
    width: 100%;
    background-color: $line;
    border: none;
    box-shadow: none;
    height: .25em;
    border-radius: 0;
    .noUi-connect{
      background-color: $primary;
    }
    .noUi-handle{
      box-shadow: none;
      border: 2px solid $primary;
      background-color: $white;
      width: 1.125em;
      height: 1.125em;
      top: -.4375em !important;
      right: -.4375em !important;
      border-radius: 50%;
      cursor: pointer;
      &:before{
        opacity: 0;
        width: 300%;
        height: 100%;
        @include centerAbsolute;
      }
      &:after{
        display: none;
      }
    }
  }
  &__inputs{
    margin-top: 1em;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap: 1.5em;
    position: relative;
    .body-s{
      @include centerAbsolute;
    }
  }
  .fg{
    height: 2.875em;
    input{
      font-size: .875em;
      padding: 0 1.1em;
      height: 100%;
      border-radius: .56em;
      color: $gray;
    }
  }
}
