.main{
  padding-top: 3em;
  &>.full-block{
    padding: 13.75em 0 1.5em;
  }
  .produce__desc{
    max-width: 32em;
  }
  .produce__bottom{
    margin-top: 2.5em;
  }
}


@media screen and (max-width: 992px){
  .main{
    .container{
      padding: 0;
    }
    &>.full-block{
      padding: 0;
    }
    .produce__top{
      padding-top: 10.625em;
      padding-bottom: 3.5em;
      min-height: unset;
    }
    .produce__desc{
      max-width: 100%;
    }
    .produce__bottom{
      margin-top: 1.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    padding-top: 2.75em;
    .produce__top{
      padding-top: 8em;
      padding-bottom: 1.5em;
    }
  }
}
