@import "_header";
@import "_header2";
@import "_header3";
@import "_header4";
@import "_footer";

.mob-footer{
  display: none;
}


@media screen and (max-width: 992px){
  .mob-footer{
    position: fixed;
    z-index: 10;
    left: 0;
    bottom: 0;
    width: 100%;
    border-radius: 1em 1em 0 0;
    height: 3.625em;
    background-color: $white;
    display: grid;
    grid-template-columns: repeat(4,25%);
    align-items: center;
    border: $border;
    border-bottom: none;
    &__btn{
      height: 100%;
      @include center;
      flex-direction: column;
      color: $gray;
      position: relative;
      &:hover{
        color: $gray;
      }
      svg,
      img{
        width: auto;
        height: 1.25em;
        margin-bottom: .125em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .mob-footer{

  }
}
