.order{
  padding-top: 2em;
  &__col{
    grid-column: span 2;
  }
  &__groups{
    @include grid100gap(2em);
  }
}

.order-group{
  &__fields{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: 1em;
  }
}

.order-info{
  padding: 2.5em;
  border-radius: 1em;
  background-color: $fon;
  position: sticky;
  top: 1em;
  &__items{
    margin-top: 1.5em;
    @include grid100gap(1.5em);
  }
  &__sum{
    text-align: right;
  }
  &__btn{
    width: 100%;
    margin-top: 1.5em;
  }
}

.cart-item,
.cart-item{
  display: grid;
  grid-template-columns: 4.5em calc(100% - 5.5em);
  gap: 1em;
  &__img{
    @include imgCover(133%);
    border-radius: .625em;
  }
  &__prices{
    @include grid100gap(.25em);
    margin-left: 1em;
  }
  &__desc{
    @include truncate(2);
  }
}

.order-item{
  &__text{
    max-width: 19em;
  }
}

.cart-item{
  &__col2{
    display: grid;
    grid-template-columns: 16em calc(100% - 18.5em);
    gap: 2.5em;
    align-items: center;
  }
  &__delete{
    width: 1.5em;
    @include center;
    img{
      width: 100%;
      transform: rotate(-45deg);
    }
  }
}

.order-pay{
  padding-top: 4em;
  &__inner{
    width: 61.25em;
    max-width: 100%;
    margin: 0 auto;
    img{
      width: 100%;
      display: block;
    }
  }
}

.order-modal{
  &__props{
    @include grid100gap(.875em);
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: 1em;
  }
  &__item{
    padding: 1.5em;
    background-color: $fon;
    border-radius: 1em;
  }
  .order-info{
    padding: 1.5em;
  }
}

._full{
  grid-column: 1/-1;
}

.prop-item{
  display: flex;
  justify-content: space-between;
  gap: .5em;
  &:after{
    content: '';
    flex-grow: 1;
    height: 1px;
    width: .25em;
    border-bottom: 1px dashed $line;
    margin-top: .875em;
  }
  &__desc{
    order: 2;
  }
  img{
    margin-right: .5em;
    width: 1.375em;
  }
}


@media screen and (max-width: 992px){
  .order{
    &__grid{
      grid-template-columns: 100%;
      gap: 3.5em;
    }
    &__col{
      grid-column: span 1;
    }
  }

  .order-info{
    padding: 2em;
  }

  .order-item{
    &__text{
      max-width: 29em;
    }
  }

  .order-modal{
    &__items{
      grid-template-columns: 100%;
    }
  }

  .cart-modal{
    .modal__dialog{
      display: inline-flex;
      flex-direction: column;
    }
    &__bottom{
      margin-top: auto;
    }
  }

  .cart-item{
    &__col2{
      grid-template-columns: 20em calc(100% - 22.5em);
    }
  }
}


@media screen and (max-width: 576px){
  .order{
    padding-top: 1.5em;
    &__grid{
      gap: 3em;
    }
    &__groups{
      gap: 1.5em;
    }
  }

  .order-group{
    &__fields{
      grid-template-columns: 100%;
      gap: .75em;
    }
  }

  .order-info{
    padding: 1.5em;
  }

  .order-item{
    grid-template-columns: 3.75em calc(100% - 4.75em);
    &__col2{
      display: block;
    }
    &__prices{
      margin: .5em 0 0;
      gap: .5em;
      display: flex;
    }
  }

  .cart-item{
    align-items: flex-start;
    grid-template-columns: 4.5em calc(100% - 5.25em);
    gap: .75em;
    &__col2{
      grid-template-columns: 100%;
      gap: .75em;
    }
  }

  .order-modal{
    &__items{
      gap: .75em;
    }
  }
}
