.catalog-categories{
  margin-bottom: 1em;
  border-radius: 1em;
  background-color: $fon;
  padding: 1.25em;
}

.catalog-items{
  row-gap: 2.5em;
  grid-auto-flow: dense;
}

.catalog-consult{
  padding: 3em;
  background-color: $black;
  color: $white;
  grid-column: 1/-1;
  border-radius: 1.5em;
  &__inner{
    max-width: 36em;
  }
}

.set{
  &__items{
    margin-top: 2em;
    display: flex;
    gap: 1em;
    flex-wrap: wrap;
  }
  &__item{
    height: 3em;
    padding: 0 1.25em;
    border-radius: 0.5em;
    background: rgba($primary, 0.12);
    color: $primary;
    @include center;
  }
}

.photo-slider{
  .swiper-slide{
    width: calc(100% / 6);
  }
  &__img{
    @include imgCover(111%);
    border-radius: 1.5em;
  }
}


@media screen and (max-width: 992px){
  .catalog-categories{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    .text-btn,
    .filter-group__top,
    .filter-group__checks{
      display: none;
    }
    .filter-group__bottom{
      margin: 0;
    }
    .m-search{
      margin: 0;
      height: 2.75em;
    }
  }

  .catalog-items{
    row-gap: 2em;
  }

  .catalog-consult{
    padding: 2em;
    &__inner{
      max-width: 27em;
    }
  }

  .set{
    &__item{
      height: 2.875em;
      span{
        font-size: .875em;
      }
    }
  }

  .photo-slider{
    .swiper-slide{
      width: calc(100% / 3);
    }
  }
}


@media screen and (max-width: 576px){
  .catalog-categories{
    padding: 1em;
  }

  .catalog-items{
    gap: 1.5em .75em;
    grid-template-columns: repeat(2,calc(50% - .375em));
  }

  .catalog-consult{
    padding: 1.5em;
    border-radius: 1em;
  }

  .set{
    &__items{
      margin-top: 1.5em;
      gap: .75em;
    }
    &__item{
      padding: 0 1em;
    }
  }

  .photo-collection{
    .h2{
      padding-right: 2em;
    }
  }

  .photo-slider{
    .swiper-slide{
      width: 8.75em;
    }
  }
}
