.product-color{
  &__items{
    display: flex;
    flex-wrap: wrap;
    margin-top: .75em;
    gap: .5em;
  }
}

.color-radio{
  padding: .25em;
  width: 2.875em;
  height: 2.875em;
  //overflow: hidden;
  &__img{
    width: 100%;
    height: 100%;
    border-radius: .5em;
    pointer-events: none;
    background-size: cover;
    &:before{
      content: '';
      @include centerAbsolute;
      width: 100%;
      height: 100%;
      border: $border;
      border-radius: inherit;
      z-index: 3;
    }
  }
  input:checked ~ .color-radio__img:before{
    border-color: $primary;
  }
  input:disabled ~ .color-radio__img{
    &:after{
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) rotate(45deg);
      width: 100%;
      height: 1px;
      background-color: $line;
    }
  }
}

.btn-radio{
  label{
    white-space: nowrap;
    @include center;
    height: 3.375em;
    border-radius: .5em;
    border: $border;
    padding: 0 1.375em;
  }
  input:checked ~ label{
    border-color: $primary;
    outline: 1px solid $primary !important;
  }
}


@media screen and (max-width: 992px){
  .btn-radio{
    label{
      height: 3.25em;
    }
  }

}


