.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background: rgba(44, 51, 47, 0.40);
    backdrop-filter: blur(8px);
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        background: #fff;
        width: 30em;
        max-width: calc(100% - 2em);
        position: relative;
        z-index: 3;
        padding: 2.5em;
        border-radius: 1em;
        display: inline-block;
    }
    .close-btn{
        position: absolute;
        top: 1.5em;
        right: 1.5em;
    }
    &__cities{
        margin-top: 1em;
        @include grid100gap(1em);
    }
}

.close-btn{
    width: 1.5em;
    @include center;
    img{
        width: 100%;
    }
}

.m-search{
    position: relative;
    img{
        position: absolute;
        top: 50%;
        right: 1em;
        transform: translateY(-50%);
        width: 1.25em;
    }
    .fg{
        height: 2.875em;
    }
    input{
        padding: 0 3em 0 1.1em;
        font-size: .875em;
        height: 100%;
        border-radius: .6em;
    }
}

.city-item{
    cursor: pointer;
    @include grid100gap(.25em);
    padding-right: 2em;
    background-image: url(../img/check-circle.svg);
    background-repeat: no-repeat;
    background-size: 0 auto;
    background-position: right center;
    &._selected{
        background-size: 1.125em auto;
    }
}

.full-modal{
    .modal__dialog{
        max-width: 100%;
        width: 47.5em;
    }
}

@media screen and (max-width: 992px){
    .modal{
        &__dialog {
            padding: 2.5em 1.875em;
        }
        .close-btn{
            top: 1.25em;
            right: 1.25em;
        }
    }

    .full-modal{
        padding: 0;
        .modal__dialog{
            border-radius: 0;
            width: 100%;
            height: $vh;
            overflow-y: auto;
        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__dialog {
            padding: 1.5em 1em;
        }
        .close-btn{
            top: .75em;
            right: .75em;
        }
    }
}
