.info-nav{
  &__show{
    display: none;
  }
  &__item{
    display: flex;
    align-items: center;
    padding: 1em;
    background-color: $fon;
    border-radius: 1.5em;
    font-weight: 700;
    .m-icon{
      margin-right: 1.5em;
    }
    &._active{
      background-color: $primary;
      color: $white;
      .m-icon{
        background-color: #76C793;
        path{
          stroke: $white;
        }
      }
    }
  }
}

._max-w84{
  max-width: 84em;
}

.delivery-table{
  &__row,
  &__head{
    display: grid;
    grid-template-columns: calc(100% - 40.5em) 18.75em 18.75em;
    gap: 1.5em;
  }
  &__head{
    padding-bottom: 1.5em;
  }
}

.delivery-item{
  padding: 1.5em 0;
  border-top: $border;
  &:last-child{
    border-bottom: $border;
  }
  &__hidden{
    grid-column: span 2;
    @media screen and (min-width: 577px){
      display: block !important;
    }
  }
  &__btn,
  &__prop-title{
    display: none;
  }
}

.contact-card{
  .m-icon._big img{
    width: 2em;
  }
}


@media screen and (max-width: 992px){

}


@media screen and (max-width: 576px){
  .info-nav {
    position: relative;
    &._open{
      .info-nav__hidden{
        opacity: 1;
        pointer-events: unset;
      }
      .info-nav__arrow{
        img{
          transform: rotate(-180deg);
        }
      }
    }
    &__show {
      display: block;
      cursor: pointer;
    }
    &__hidden{
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
      position: absolute;
      top: 100%;
      margin-top: .375em;
      border-radius: .375em;
      box-shadow: 0px 6px 16px 0px rgba(20, 20, 20, 0.10);
      border: none;
      background: #FFF;
      max-height: 20.75em;
      overflow-y: auto;
      z-index: 5;
      @include scroll;
      padding: .375em;
      width: 100%;
      .info-nav__item{
        background-color: transparent;
        font-size: 1em;
        line-height: 150%;
        padding: .5em;
        &._active{
          background-color: transparent;
          color: $primary;
        }
        .m-icon{
          display: none;
        }
      }
    }
    &__item{
      border-radius: 1em;
      .m-icon{
        margin-right: 1em;
      }
    }
    &__arrow{
      width: 2.25em;
      height: 2.25em;
      background-color: $white;
      border-radius: .5em;
      margin-left: auto;
      @include center;
      img{
        width: .75em;
        transition: all .3s;
      }
    }
  }

  .delivery-table{
    &__head{
      display: none;
    }
  }

  .delivery-item{
    padding: 1.25em 0;
    display: block;
    &__show{
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__hidden{
      margin-top: 1em;
      display: none;
    }
    &__props{
      gap: 1em;
      grid-template-columns: 100%;
    }
    &__prop{
      display: flex;
      align-items: center;
      justify-content: space-between;
      &-title{
        display: block;
      }
    }
    &__btn{
      width: 2.25em;
      height: 2.25em;
      border-radius: 0.5em;
      background-color: $fon;
      @include center;
      svg{
        width: .75em;
      }
    }
    &._open{
      .delivery-item__btn{
        svg{
          transform: rotate(-180deg);
        }
        path{
          stroke: $primary;
        }
      }
    }
  }
}
