.about-main{
  color: $white;
  padding: 6.5em 0;
  &__inner{
    max-width: 48.25em;
  }
}

.how-work{
  @media screen and (min-width: 993px){
    .advantage-card{
      grid-column: span 2;
      &:nth-child(4),
      &:nth-child(5){
        grid-column: span 3;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .about-main{
    padding: 3.5em 0;
    &__inner{
      min-height: 30em;
    }
  }
}


@media screen and (max-width: 576px){
  .about-main{
    padding: 3em 0;
  }
}
