.catalog-item{
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 40.5em) 36.5em;
    gap: 1.5em 4em;
  }
  &__rating{
    gap: .5em;
  }
  &__share{
    display: flex;
    align-items: center;
    color: $gray;
    margin-left: 1.5em;
    &:hover{
      color: $primary;
      path{
        stroke: $primary;
      }
    }
    svg{
      width: 1.125em;
      margin-right: .375em;
    }
  }
}

.gallery-slider{
  @media screen and (min-width: 993px){
    .swiper-container{
      width: 100%;
    }
    .swiper-slide{
      width: 100%;
      padding: 0;
    }
    .swiper-wrapper{
      display: grid;
      gap: 1.5em;
      grid-template-columns: repeat(2,calc(50% - .75em));
      transform: unset !important;
    }
  }
  &__img{
    @include imgCover(133%);
    border-radius: .625em;
  }
}

.catalog-sidebar{
  position: sticky;
  top: 1em;
}

.product-cart{
  &__top{
    position: relative;
    z-index: 2;
    border-radius: 1.5em;
    background: $white;
    box-shadow: $shadow;
    padding: 1.25em;
  }
  &__prices{
    margin-top: 1em;
    gap: 1.5em;
  }
  &__available{
    img{
      width: .75em;
      margin-right: .375em;
    }
  }
  &__btn{
    width: 100%;
    margin-top: 1.5em;
  }
  &__bottom{
    @include grid100gap(.75em);
    padding: 2.5em 1.25em 1.25em;
    border-radius: 0 0 1.5em 1.5em;
    background: $fon;
    margin-top: -1.25em;
    @include grid100gap(.75em);
  }
}

.product-car{
  padding: 1.25em;
  border-radius: 1.5em;
  background-color: $fon;
  display: grid;
  grid-template-columns: 3em calc(100% - 7.125em) 2.125em;
  gap: 1em;
  align-items: center;
  &__img{
    @include imgCover(100%);
    border-radius: .5em;;
  }
  &__btn{
    @include center;
    width: 2.125em;
    height: 2.125em;
    border-radius: 0.5em;
    background: #EBEBEB;
    img{
      width: 1.125em;
    }
  }
  &__desc{
    @include truncateLine;
  }
}


@media screen and (max-width: 992px){
  .catalog-item{
    overflow: hidden;
    &__grid{
      grid-template-columns: 100%;
    }
    &__info{
      display: block;
      &-col{
        justify-content: space-between;
        margin-top: 1em;
      }
    }
  }

  .gallery-slider{
    .swiper-container{
      width: calc(100% + 1em);
    }
    .swiper-slide{
      width: 22.25em;
      padding-right: 1em;
    }
  }

  .catalog-sidebar{
    display: flex;
    flex-direction: column;
  }

  .product-cart{
    order: 2;
  }

  .product-car{
    order: 3;
  }
}


@media screen and (max-width: 576px){
  .gallery-slider{
    .swiper-container{
      width: calc(100% + .75em);
    }
    .swiper-slide{
      width: 14.5em;
      padding-right: .75em;
    }
  }

  .product-car{
    margin-top: .75em;
  }
}
