.main-catalog{
  &__inner{

  }
}

.product-slider{
  .swiper-slide{
    width: calc(100% / 7);
  }
  .swiper-nav{
    display: none;
  }
}

.product-card{
  height: 100%;
  display: flex;
  flex-direction: column;
  &__img{
    @include imgCover(133%);
    border-radius: .75em;
  }
  &__reviews{
    img{
      width: 1em;
      margin-right: .125em;
    }
    &-count{
      margin-left: .5em;
    }
  }
  &__nav{
    margin-top: auto;
    padding-top: 1em;
    display: flex;
  }
  &__btn{
    padding: 0 1.75em;
    height: 3.5em;
  }
  &__btn2{
    padding: 0 1.5em;
    height: 3.5em;
    margin-left: .5em;
    span{
      display: none;
    }
    img{
      margin: 0;
    }
  }
}

.m-prices{
  gap: .75em;
}

.decor{
  &__img{
    @include imgCover(100%);
    border-radius: .625em;
  }
}

.materials-slider{
  .swiper-nav{
    right: unset;
    left: 50%;
    bottom: 3em;
    margin: 0 0 0 3.75em;
  }
  .swiper-button{
    background-color: $white;
  }
}

.material-card{
  &__img{
    border-radius: 1.5em;
    height: 100%;
    min-height: 27.5em;
    @include imgCover(0);
  }
  &__content{
    padding: 3em 3em 6em;
    border-radius: 1.5em;
    background-color: $fon;
  }
}

.m-img{
  img{
    width: 100%;
    display: block;
    border-radius: 1.5em;
  }
  ._view2,
  ._view3{
    display: none;
  }
}


@media screen and (max-width: 992px){
  .main-catalog{
    &__inner{
      position: relative;
      padding-bottom: 5.625em;
    }
    &__link{
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
    }
  }

  .product-slider{
    .swiper-slide{
      width: calc(100% / 3);
    }
    .swiper-nav{
      display: flex;
    }
  }

  .materials-slider{
    .swiper-nav{
      left: 2em;
      bottom: 2em;
      margin: 0;
    }
  }

  .material-card{
    &__img{
      min-height: 23.75em;
    }
    &__content{
      padding: 2em 2em 6.5em;
    }
  }

  .m-img{
    ._view2{
      display: block;
    }
    ._view1{
      display: none;
    }
  }
}


@media screen and (max-width: 576px){
  .product-slider{
    .swiper-slide{
      width: 50%;
    }
  }

  .product-card{
    &__btn{
      display: none;
    }
    &__btn2{
      margin: 0;
      width: 100%;
      padding: 0;
      gap: .5em;
      span{
        display: block;
      }
    }
  }

  .materials-slider{
    .swiper-nav{
      left: 1.5em;
      bottom: 1.5em;
    }
  }

  .material-card{
    &__img{
      min-height: 20em;
    }
    &__content{
      padding: 1.5em 1.5em 5em;
    }
  }

  .m-img{
    img{
      border-radius: 1em;
    }
    ._view3{
      display: block;
    }
    ._view2{
      display: none;
    }
  }
}
