.nav{
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__link{
    margin-right: 2em;
    &:last-child{
      margin-right: 0;
    }
  }
}

.nav__link,
.nav-item__top{
  font-weight: 600;
  span{
    font-size: 1em;
    line-height: 150%;
  }
}

.nav-item{
  position: relative;
  margin-right: 2em;
  &__top{
    cursor: pointer;
    display: flex;
    align-items: center;
    &:after{
      content: '';
      margin-left: .375em;
      flex-shrink: 0;
      width: 1em;
      height: 1em;
      background-image: url(../img/arrow-dropdown.svg);
      @include bgImgSize(100%);
    }
  }
  &__list{
    a{
      font-size: .875em;
      line-height: 143%;
      padding: .375em;
      border-radius: .25em;
      display: flex;
      align-items: center;
      justify-content: space-between;
      &:hover{
        color: $primary;
      }
    }
    li{
      &:last-child{
        border-bottom: none;
      }
    }
  }
  @media screen and (min-width: 993px){
    &:hover{
      .nav-item__bottom{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &__bottom{
      color: $black;
      position: absolute;
      top: 100%;
      left: -1.5em;
      padding-top: .75em;
      margin-top: -1px;
      width: auto;
      white-space: nowrap;
      z-index: 4;
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
      display: block !important;
    }
    &__inner{
      padding: 1em;
      background-color: $white;
      border-radius: .375em;
      box-shadow: 0 6px 16px 0 rgba(20,20,20,.1);
    }
  }
}

.catalog-contacts{
  margin-top: auto;
  display: flex;
  gap: 3em;
  align-items: flex-start;
  &__item{
    @include grid100gap(.5em);
  }
}


@media screen and (max-width: 992px){
  .menu{
    position: fixed;
    background: rgba($black,.6);
    width: 100%;
    height: $vh;
    top: 0;
    left: 0;
    opacity: 0;
    transition: all .2s;
    pointer-events: none;
    &__scroll,
    &__top{
      position: fixed;
      top: 0;
      left: 0;
      width: 33.75em;
      background-color: $white;
    }
    &__scroll{
      height: $vh;
      z-index: 10;
      padding: 5.625em 1.25em 1.25em;
      overflow-y: auto;
      display: flex;
      flex-direction: column;
    }
    &__top{
      z-index: 11;
      padding: 1.25em;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__bg{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: $vh;
      display: block;
    }
    &__close{
      @include center;
    }
  }

  .nav{
    display: block;
    padding: 0;
    &__link{
      margin: 0 0 1em;
      background-color: $fon;
      border-radius: .625em;
      &:hover{
        color: $black;
      }
      &:last-child{
        margin: 0;
        border: none;
      }
    }
  }

  .nav__link,
  .nav-item__top{
    display: flex;
    align-items: center;
    padding: .75em 1em;
    color: $black;
  }

  .nav-item{
    margin: 0 0 1em;
    background-color: $fon;
    border-radius: .625em;
    &._active{
      .nav-item__top:after{
        transform: rotate(-180deg);
      }
    }
    &__list{
      @include grid100gap(.75em);
      a{
        padding: 0;
        font-size: .75em;
      }
    }
    &__bottom{
      display: none;
      padding: 0 1em .75em;
      //margin-top: -.5em;
    }
  }

  .menu-bottom{
    padding-top: 2em;
    margin-top: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    &__info{
      text-align: right;
      margin-right: 1.5em;
    }
  }

  .catalog-contacts{
    display: none;
  }
}


@media screen and (max-width: 576px){
  .menu{
    &__scroll,
    &__top{
      width: 18.625em;
    }
    &__scroll{
      padding: 5.125em 1em 1.25em;
    }
    &__top{
      padding: 1.25em 1em;
    }
  }

  .menu-bottom{
    display: block;
    &__content{
      display: block;
    }
    &__info{
      text-align: left;
      margin: 1em 0;
    }
    &__link{
      width: 100%;
    }
  }
}
