.header{
  position: relative;
  z-index: 10;
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 7.4375em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.header-top{
  padding: .75em 0;
}

.header-city{
  &__btn{
    margin-right: 1em;
  }
  &__mark{
    width: 1.25em;
    margin-right: .5em;
  }
  &__arrow{
    width: 1em;
    margin-left: .5em;
  }
}

.menu-bottom,
.menu__top,
.menu__bg,
.burger{
  display: none;
}

.header-shops{
  .socials{
    margin-left: 1em;
    &__item{
      width: 1.5em;
      height: 1.5em;
      @include lastM(0 .5em 0 0);
    }
  }
}

.header-bottom{
  padding: .75em 2.5em;
  border-radius: 1em;
  background-color: $fon;
  display: flex;
  align-items: center;
  &__search{
    margin-right: 2.5em;
    img{
      width: 1.5em;
      margin-right: .75em;
    }
  }
}

.header-contacts{
  &__phone{
    text-align: right;
    margin-right: 1.5em;
    img{
      display: none;
    }
  }
}

.header-cart{
  display: flex;
  align-items: center;
  cursor: pointer;
  margin-left: 2.5em;
  img{
    width: 2em;
    margin-right: .5em;
  }
}

.catalog-btn{
  padding: 0 1.5em;
  flex-shrink: 0;
  height: 3.5em;
  @include center;
  font-weight: 600;
  border-radius: .75em;
  margin: 0 2em 0 2.5em;
  &__img{
    width: 1.125em;
    flex-shrink: 0;
    margin-right: .375em;
    position: relative;
    img{
      width: 100%;
      @include centerAbsolute;
      transition: all .3s;
    }
    ._view2{
      opacity: 0;
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    &._open{
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
      .mob-footer{
        opacity: 0;
        pointer-events: none;
      }
    }
  }

  .header-bottom {
    padding: .75em 1.5em;
    &__search{
      display: none;
    }
  }

  .burger{
    @include center;
    width: 1.5em;
    margin-right: 1.5em;
    img{
      width: 100%;
    }
  }

  .catalog-btn,
  .header-cart{
    display: none;
  }
}


@media screen and (max-width: 576px){
  .header{
    &__logo{
      width: 4.875em;
    }
  }

  .header-shops{
    display: none;
  }

  .header-bottom {
    padding: 1em;
  }

  .header-contacts{
    &__phone{
      margin-right: 1em;
      .body-s{
        display: none;
      }
      &-link{
        @include center;
        img{
          display: block;
          width: 1.5em;
        }
        span{
          display: none;
        }
      }
    }
    .socials{
      &__item{
        width: 2em;
        height: 2em;
        @include lastM(0 .625em 0 0);
      }
    }
  }

  .burger{
    margin-right: 1em;
  }
}
