.point-item2{
  display: inline-grid;
  grid-template-columns: 100%;
  gap: .5em;
}

.contacts-points{
  .h2{
    button{
      font-weight: 600;
    }
  }
  &__items{
    column-count: 4;
    column-gap: 1.5em;
  }
  .point-item2{
    width: 100%;
    margin-bottom: 1.5em;
  }
  &__more{
    display: none;
    ._view2{
      display: none;
    }
  }
}


@media screen and (max-width: 992px){
  .contacts-points{
    &:not(._show-all){
      .point-item2:nth-child(20)~.point-item2{
        display: none;
      }
    }
    &._show-all{
      .contacts-points__more{
        ._view2{
          display: block;
        }
        ._view1{
          display: none;
        }
      }
    }
    &__items{
      column-count: 2;
    }
    &__more{
      display: flex;
    }
  }
}


@media screen and (max-width: 576px){
  .contacts-points{
    &:not(._show-all){
      .point-item2:nth-child(10)~.point-item2{
        display: none;
      }
    }
    &__items{
      column-count: 1;
    }
    .point-item2{
      margin-bottom: .75em;
    }
    &__more{
      margin-top: .75em;
    }
  }
}
