.advantages2 {
  &__cards {
    display: grid;
    grid-template-columns: 100%;
    gap: 4em;
  }
}
.advantage2-card {
  &__grid {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 1.5em / 2));
    gap: 1.5em;
  }
  &__img {
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 1.5em;
    }
    ._view2,
    ._view3{
      display: none;
    }
  }
  &__desc {
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: $fon;
    border-radius: 1.5em;
    padding: 3em;
  }
}

@media screen and (max-width: 992px) {
  .advantages2 {
    &__cards {
      gap: 3.5em;
    }
  }
  .advantage2-card {
    &__grid {
      grid-template-columns: 100%;
    }
    &__img {
      position: relative;
      order: -1;
      padding-bottom: 53.673%;
      img {
        position: absolute;
        top: 0;
        left: 0;
        border-radius: 1em;
        object-fit: cover;
      }
      ._view1 {
        display: none;
      }
      ._view2 {
        display: block;
      }
    }
    &__desc {
      border-radius: 1em;
      padding: 2em;
    }
    .text {
      gap: 0.75em;
    }
  }
}

@media screen and (max-width: 576px) {
  .advantages2 {
    &__cards {
      gap: 3em;
    }
  }
  .advantage2-card {
    &__grid {
      gap: 0.75em;
    }
    &__img {
      padding-bottom: 97.565%;
      ._view2 {
        display: none;
      }
      ._view3 {
        display: block;
      }
    }
    &__desc {
      padding: 1.5em;
    }
    .text {
      h2 {
        font-size: 1.375em;
      }
      h4 {
        font-size: 1.125em;
      }
    }
  }
}
