.header{
  &._open-search{
    .header-search{
      opacity: 1;
      pointer-events: unset;
    }
    .header-search__bg{
      display: block;
    }
  }
}

.m-close,
.header-action__phone,
.menu__close{
  width: 2.375em;
  height: 2.375em;
  border-radius: 0.625em;
  background-color: $fon;
  img{
    width: 1em;
  }
}

.m-close{
  @include center;
}

.header-search__field{
  flex-grow: 1;
  position: relative;
  &._toggle-search{
    cursor: pointer;
    input{
      cursor: pointer;
    }
    .m-search__btn2{
      pointer-events: none;
    }
  }
  .fg{
    height: 3.5em;
  }
  input{
    height: 100%;
    background-color: $fon;
    border-color: $fon;
    padding-left: 3.25em;
    padding-right: 5.5em;
  }
  .m-search{
    flex-grow: 1;
    position: relative;
    &__btn2{
      height: 100%;
      position: absolute;
      top: 0;
      right: 0;
      width: 4.5em;
      padding: 0;
      @include center;
      img{
        width: 1.5em;
        transform: unset;
        margin: 0 !important;
        position: static;
      }
    }

    &__img{
      position: absolute;
      left: 1em;
      width: 1.5em;
      top: 50%;
      transform: translateY(-50%);
    }
  }
}

.header-search{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  max-height: $vh;
  overflow-y: auto;
  padding: 1.5em 0;
  background-color: $white;
  z-index: 1000;
  opacity: 0;
  pointer-events: none;
  &__close{
    margin-left: 1em;
    width: 3.5em;
    height: 3.5em;
    img{
      width: 1.5em;
    }
  }
}

.header-search__bg{
  z-index: 999;
  display: none;
}



