.photo-collection._small{
  @media screen and (min-width: 993px){
    .photo-collection__inner{
      width: 84em;
    }
    .swiper-slide{
      width: 20%;
    }
  }
}

.reviews-collection{
  @media screen and (min-width: 993px){
    &__grid{
      align-items: center;
    }
    &__col{
      grid-column: span 3;
    }
    .swiper-slide{
      width: calc(100% / 3);
    }
  }
}

.rating--green{
  .rating-input__item{
    &:hover {
      cursor: pointer;
      path {
        fill: $primary;
      }
      &~.rating-input__item {
        path {
          fill: $primary;
        }
      }
    }
  }
  .rating-input[data-total-rating="1"]:not(:hover) .rating-input__item:nth-child(n+5),
  .rating-input[data-total-rating="2"]:not(:hover) .rating-input__item:nth-child(n+4),
  .rating-input[data-total-rating="3"]:not(:hover) .rating-input__item:nth-child(n+3),
  .rating-input[data-total-rating="4"]:not(:hover) .rating-input__item:nth-child(n+2),
  .rating-input[data-total-rating="5"]:not(:hover) .rating-input__item:nth-child(n+1) {
    path {
      fill: $primary;
      stroke: $primary;
    }
  }
}

.reviews-info{
  border-radius: 1.5em;
  padding: 1.25em;
  background-color: $fon;
  &__text{
    margin-top: 1em;
    padding-top: 1em;
    border-top: $border;
  }
  .rating-input__item{
    width: 2em;
    margin-right: .25em;
  }
}

.rating-stat{
  table{
    width: 100%;
    border-spacing: 0 .5em;
  }
  td{
    width: auto;
    padding: 0;
    white-space: nowrap;
    &:nth-child(2){
      width: 100%;
      padding: 0 .5em;
    }
  }
}

.progress{
  height: .375em;
  border-radius: 1.25em;
  background-color: $line;
  position: relative;
  span{
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    max-width: 100%;
    border-radius: inherit;
    background-color: $primary;
  }
}

.props{
  &__items{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1em));
    gap: .75em 2em;
  }
}


@media screen and (max-width: 992px){
  .props{
    &__items{
      grid-template-columns: 100%;
    }
  }
}


